import React, {useEffect, useState} from 'react';
import Note from './components/Note'
import  './App.css'
import Form from './components/Form'
import notesApi from './services/notesApi'
const App = () => {
    const [notes, setNotes] = useState([])
    const [newNote, setNewNote] = useState('')
    const [showAll , setShowAll] = useState(true)
    const handleNoteChange = (event) => setNewNote(event.target.value)
    const handleFilter = () => setShowAll(!showAll) 
    const handleButton = (note, index) => {
        notes[index].important = !note.important;
        setNotes([...notes])
    }
    const deleteNote = async(event) => {
      event.preventDefault()
      const id = event.target[0].value;
      const newNotes = notes.filter(note => note._id !==id)
      let isSure = window.confirm(`Are you sure you want to delete this note?`);
      if(isSure){
        setNotes([...newNotes])
        await notesApi.deleteNote(id)
      }
    }
    const addNewNote = async (event) => {
        event.preventDefault()
        const note = {
            content: newNote
        }
        let result = await notesApi.addNote(note)
        console.log(result)
        setNotes(notes.concat(result))
        setNewNote('')
    }
    const getAll = async() => {
      const returnedNotes = await notesApi.getAll();
      setNotes(returnedNotes);
    }
    useEffect(()=> {
      getAll()
    }, []);
    return (
        <div>
          <h1>Notes</h1>
          <button onClick={handleFilter} className='btn-green'>{showAll ? 'Show important' : 'Show All'}</button> 
          <ul>
            { notes.map((note, index) => <Note key={note._id} showAll={showAll} note={note} handleDelete={deleteNote} handleButton={() => handleButton(note , index)}/>) }
          </ul>
          <Form handleForm={addNewNote}  title='Adding New Note' label='Description' value={newNote} handleInput={handleNoteChange} buttonDescription='Add New Note'/>
        </div>
      )
}


export default App;