import React from 'react'


const Note = ({showAll, note, handleDelete, handleButton}) => {
    if(showAll){
        return(
            <div className='inline-flex'>
                <li>{note.content}</li>
                <button onClick={handleButton} className='btn-prime'>{note.important ? 'make not important': 'make important'}</button>
                <form onSubmit={handleDelete}>
                    <input type='hidden' value={note._id}/>
                    <button type='submit' className='btn-danger'>Delete Note</button>
                </form>
            </div>
        )
    }
    if(note.important){
        return(
            <div className='inline-flex'>
            <li>{note.content}</li>
            <button onClick={handleButton} className='btn-prime'>{note.important ? 'make not important': 'make important'}</button>
            <form onSubmit={handleDelete}>
                <input type='hidden' value={note._id}/>
                <button type='submit' className='btn-danger'>Delete Note</button>
            </form>
        </div>
        )
    }
    return(null)
}

export default Note;    