import React from 'react'


const Form = ({handleForm, title, label, value, handleInput, buttonDescription}) => {
    return(
        <div>
            <form onSubmit={handleForm}>
                <legend><strong>{title}</strong></legend>
                <div className='form-group'>
                <label >{label}</label>
                <input type='text' name={label} onChange={handleInput} value={value}></input>
              </div>
              <button type='submit' className='btn-primary'>{buttonDescription}</button>
            </form>
        </div>
    )
}

export default Form;